const Menu = [
  {
    title: "",
    group: "apps",
    icon: "dashboard",
    name: "Dashboard"
  },
  /*{ header: "CMS" },*/
  {
    title: "",
    group: "question",
    icon: "live_help",
    name: "Ask"
  },
  {
    title: "",
    group: "answer",
    icon: "edit",
    name: "Solve"
  }
]
// reorder menu
Menu.forEach(item => {
  if (item.items) {
    item.items.sort((x, y) => {
      let textA = x.title.toUpperCase()
      let textB = y.title.toUpperCase()
      return textA < textB ? -1 : textA > textB ? 1 : 0
    })
  }
})

export default Menu
